@import "https://fonts.googleapis.com/css2?family=Bubblegum+Sans&display=swap";
body {
  background-color: #fff5f5;
  font-family: Bubblegum Sans, cursive;
}

.breed-select {
  color: #333;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 1rem;
}

.error {
  color: red;
}

.cat-info {
  margin-top: 20px;
}

.cat-info .wrapper {
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.cat-info h2 {
  color: #ff69b4;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  font-size: 2.5rem;
}

.cat-info .cat-description {
  color: #666;
  font-style: italic;
}

.loader {
  width: 48px;
  height: 48px;
  perspective: 1000px;
  width: 480px;
  height: 480px;
  color: #000;
  border-radius: 50%;
  margin-left: 35%;
  margin-right: 50px;
  animation: rotate 1s linear infinite;
  position: relative;
  transform: rotateZ(45deg);
}

.loader:before, .loader:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

.is-hidden {
  display: none;
}

